import * as fromProducts from '@store/actions/models/products/products.action';

import { ProductsInterface } from '@core/interfaces/products.interface';
import { Product } from '@core/models/products/product';

export interface ProductsState {
  types_account: ProductsInterface;
  loading: boolean;
  loaded: boolean;
  error: boolean;
}

export const initProducts: ProductsState = {
  types_account: {
    DEPOSIT_ACCOUNT: [],
    CREDIT_CARD: [],
    CURRENT_ACCOUNT: [],
    CERTIFIED_DEPOSIT_TERM: []
  },
  loading: false,
  loaded: false,
  error: false
};

export function productsReducer(state: ProductsState, action: fromProducts.actions): ProductsState {

  switch (action.type) {

    case fromProducts.LOAD_PRODUCTS:
      return {
        ...state,
        loading: true
      };

    case fromProducts.LOAD_PRODUCTS_SUCCESS:

      return {
        ...state,
        loaded: true,
        loading: false,
        types_account: (action as fromProducts.LoadProductsSuccessAction).products
      };

    case fromProducts.LOAD_PRODUCTS_FAIL:
      return {
        ...state,
        loading: false,
        loaded: false,
        error: true
      };

    default:
      return state;
  }

}
