import { Action } from '@ngrx/store';

import { Movement } from '@core/models/movement/movement';


export const LOAD_MOVEMENT = 'LOAD_MOVEMENT';
export const LOAD_MOVEMENT_SUCCESS = 'LOAD_MOVEMENT_SUCCESS';
export const MOVEMENT_FAIL = 'MOVEMENT_FAIL';
export const RESET_MOVEMENT = 'RESET_MOVEMENT';

export class LoadMovementAction implements Action {
    readonly type: string = LOAD_MOVEMENT;

    constructor(public typeAccount: string, public accountId: string) {  }
}

export class LoadMovementSuccessAction implements Action {
    readonly type: string = LOAD_MOVEMENT_SUCCESS;

    constructor(public movement: Movement ) { }
}

export class LoadMovementFailAction implements Action {
    readonly type: string = MOVEMENT_FAIL;
}

export class ResetMovementAction implements Action {
  readonly type: string = RESET_MOVEMENT;
}

export type actions = LoadMovementAction | LoadMovementSuccessAction | LoadMovementFailAction | ResetMovementAction;
