import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '@modules/auth/services/auth.service';

import { UserData } from '@core/models/user/userData';
import { AuthModel } from '@modules/auth/auth.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit {

  public userLogged: UserData;
  public registerForm: FormGroup;
  public submitted: Boolean = false;

  public iuser: UserInterface = {
    documentType: '',
    documentNumber: '',
    password: '',
    companyId: '0002',
    deviceSerial: 'versionexpt%3D'
  };

  constructor(
    private formBuilder: FormBuilder, private oServ: AuthService,
    private model: AuthModel
  ) { }

  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      tipo: ['', [Validators.required]],
      cedula: ['', [Validators.required, Validators.minLength(7)]],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }
  
  get f(): any { return this.registerForm.controls; }

  onSubmit(): void {
      this.submitted = true;
      if (this.registerForm.invalid) {
        return;
      } else {        
         this.oServ.doLogin(this.iuser).subscribe(data => {
          this.userLogged = data;

          if (this.userLogged.token) {
            this.model.fetchUser();
          }
        });  
      }
  }
  
  onChangeType(data: any): void {
    this.iuser.documentType = data.target.value;
  }
}

interface UserInterface {
  documentType: string;
  documentNumber: string;
  password: string;
  companyId: string;
  deviceSerial: string;
}
