import * as fromMovements from '@store/actions/models/movements/movement.action';
import { Movement } from '@core/models/movement/movement';

export interface MovementsState {
  account: Movement;
  loading: boolean;
  loaded: boolean;
  error: boolean;
}

export const initMovements: MovementsState = {
  account: null,
  loading: false,
  loaded: false,
  error: false
};


export function movementReducer(state: MovementsState = initMovements, action: fromMovements.actions): MovementsState {

  switch (action.type) {

      case fromMovements.LOAD_MOVEMENT:
          return {
            ...state,
            loading: true
          };

      case fromMovements.LOAD_MOVEMENT_SUCCESS:

          const actionSuccess = (action as fromMovements.LoadMovementSuccessAction);
          return {
            ...state,
            loading: false,
            loaded: true,
            account: actionSuccess.movement
          };

      case fromMovements.MOVEMENT_FAIL:
          return {
            ...state,
            loaded: false,
            loading: false,
            error: true
          };

    case fromMovements.RESET_MOVEMENT:
      return {
        account: null,
        loading: false,
        loaded: false,
        error: false
      };

      default:
          return state;
  }

}
