import { catchError, map, switchMap } from 'rxjs/operators';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Observable, of } from 'rxjs';

import { AuthService } from '@modules/auth/services/auth.service';
import * as loginActions from '@store/actions/models/auth/auth.action';
import { UserData } from '@app/core/models/user/userData';
import { Router } from '@angular/router';

@Injectable()
export class AuthEffects {

  constructor(
    private actions$: Actions,
    private loginService: AuthService,
    private router: Router
  ) { }

  @Effect()
    Login: Observable<Action> = this.actions$.pipe(
      ofType<loginActions.LoginAction>(loginActions.LOGIN),
      switchMap((action: loginActions.LoginAction) => {
        return this.loginService.getCurrentUserValue()
          .pipe(
            map((user: UserData) => { 
                this.router.navigate(['/']);
                return new loginActions.LoginSuccessAction(user); }),
            catchError(_ => of(new loginActions.LoginFailAction()))
          );
      })
    );
}
