import { Action } from '@ngrx/store';
import { UserData } from '@core/models/user/userData';


export const LOGIN = 'LOGIN';
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const LOGIN_FAIL = 'LOGIN_FAIL';

export class LoginAction implements Action {
  readonly type: string = LOGIN;
}

export class LoginSuccessAction implements Action {
  readonly type: string = LOGIN_SUCCESS;

  constructor(public user: UserData) { }
}

export class LoginFailAction implements Action {
  readonly type: string = LOGIN_FAIL;
}

export type actions = LoginAction | LoginSuccessAction | LoginFailAction;
