import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment as ENV } from '@environment';
import { UserData } from '@core/models/user/userData';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private currentUserSubject: BehaviorSubject<UserData>;
  public currentUser: Observable<UserData>;
  public token: string = '';

  constructor(private http: HttpClient) {
    this.currentUserSubject = new BehaviorSubject<UserData>(JSON.parse(localStorage.getItem('currentUser')));
    this.currentUser = this.currentUserSubject.asObservable();
  }

  /**
   * Return logged user
   */
  public getCurrentUserValue(): Observable<UserData> {
    return this.currentUser;
  }

  public getTokenDecoded(): any {
    return atob(this.token);
  }

  /**
   * Makes the login process, based on the object sent by the view
   */
  public doLogin(_userData: any): Observable<UserData> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Authorization': 'Basic YXV0aC11c2VyOnRoaXNtdXN0YmVjaGFuZ2VkIQ=='
      })
    };

    return this.http.post<UserData>(ENV.api.baseLogin + ENV.api.services.login, _userData, httpOptions).pipe(map(user => {
      // login successful if there's a jwt token in the response
      if (user && user.token) {
          this.token = user.token;
          // store user details and jwt token in local storage to keep user logged in between page refreshes
          localStorage.setItem('currentUser', JSON.stringify(user));
          this.currentUserSubject.next(user);
      }
      return user;
    }));
  }

  public logout(): void {
    localStorage.removeItem('currentUser');
    this.currentUserSubject.next(null);
  }
}
