import { ApplicationModel } from '@app/application.model';
import { Injectable } from '@angular/core';
import { select } from '@ngrx/store';
import { Observable } from 'rxjs';

import { LoginAction } from '@store/actions/models/auth/auth.action';
import { UserData } from '@app/core/models/user/userData';


@Injectable()
export class AuthModel extends ApplicationModel {

    public isLoading$: Observable<boolean> = this.store.pipe(
        select(store => store.models.user.loading)
    );

    public isErrorLogin$: Observable<boolean> = this.store.pipe(
      select(store => store.models.user.error)
    );

    public userData$: Observable<UserData> = this.store.pipe(
      select(store => store.models.user.data)
    );

    public fetchUser(): void {
        this.store.dispatch(new LoginAction());
    }
}
