import { Action } from '@ngrx/store';

import { ProductsInterface } from '@core/interfaces/products.interface';


export const LOAD_PRODUCTS = '[Products Home] Load Products';
export const LOAD_PRODUCTS_SUCCESS = '[Products Home] Load Products Success';
export const LOAD_PRODUCTS_FAIL = '[Products Home] Load Products Fail';

export class LoadProductsAction implements Action {
  readonly type: string = LOAD_PRODUCTS;
}

export class LoadProductsSuccessAction implements Action {
  readonly type: string = LOAD_PRODUCTS_SUCCESS;

  constructor(public products: ProductsInterface) { }
}

export class LoadProductsFailAction implements Action {
  readonly type: string = LOAD_PRODUCTS_FAIL;
}

export type actions = LoadProductsAction | LoadProductsFailAction | LoadProductsSuccessAction;
