import { APP_INITIALIZER, InjectionToken, LOCALE_ID, NgModule } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { INITIAL_STATE, StoreModule } from '@ngrx/store';
import { registerLocaleData } from '@angular/common';
import localeEs from '@angular/common/locales/es-US';
import { EffectsModule } from '@ngrx/effects';
import { GeneralInterceptor } from './core/interceptor/general.interceptor';

import { INITIAL_APPLICATION_STATE } from '@store/state/application.state';
import { environment as ENV } from '../environments/environment';
import { isMobile, onAppInit, translateConfig } from './app.translate';
import { rootReducer } from '@store/reducers/root.reducer';
import { AppPreloadStrategy } from './app-preload-strategy';
import { AppRoutingModule } from './app-routing.module';
import { AuthModule } from '@modules/auth/auth.module';
import { AppComponent } from './app.component';
import { CoreModule } from '@core/core.module';
import { CoreEffects } from '@core/effects';
import { ErrormanagementComponent } from './core/errormanagement/errormanagement.component';
import { ErrormanagementService } from './core/services/errormanagement/errormanagement.service';

registerLocaleData(localeEs, 'es-US');
export const REDUCER_TOKEN = new InjectionToken('Registered Reducers');

@NgModule({
  declarations: [
    AppComponent,
    ErrormanagementComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    CoreModule,
    AuthModule,
    TranslateModule.forRoot(translateConfig),
    StoreModule.forRoot(REDUCER_TOKEN),
    EffectsModule.forRoot([
      ...CoreEffects
    ]),
    ENV.storeDev

  ],
  providers: [
    AppPreloadStrategy,
    { provide: 'isMobile', useFactory: isMobile, deps: [] },
    {
      provide: APP_INITIALIZER,
      useFactory: onAppInit,
      multi: true,
      deps: [
        HttpClient,
        TranslateService
      ]
    },
    {
      provide: INITIAL_STATE,
      useValue: INITIAL_APPLICATION_STATE
    },
    {
      provide: REDUCER_TOKEN,
      useValue: rootReducer
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: GeneralInterceptor,
      multi: true
    },
    {
    provide: LOCALE_ID,
      useValue: 'es-US'
    },
    ErrormanagementService
  ],
  entryComponents: [
    ErrormanagementComponent
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }
