import * as fromAuth from '@store/actions/models/auth/auth.action';
import { UserData } from '@core/models/user/userData';

export interface UserState {
  data: UserData;
  loading: boolean;
  loaded: boolean;
  error: boolean;
}

export const initUser: UserState = {
  data: new UserData ,
  loading: false,
  loaded: false,
  error: false
};

export function userReducer(state: UserState, action: fromAuth.actions): UserState {

  switch (action.type) {

    case fromAuth.LOGIN:
      return {
        ...state,
        loading: true
      };
    case fromAuth.LOGIN_SUCCESS:
      return {
        ...state,
        loaded: true,
        data: (action as fromAuth.LoginSuccessAction).user
      };

    case fromAuth.LOGIN_FAIL:
      return {
        ...state,
        loading: false,
        loaded: false,
        error: true
      };

    default:
      return state;
  }

}
