import * as fromTransfer from '../../../actions/models/transfer-account/transfer-account-action';

export interface AccountTransferState {
  loading: boolean;
  loaded: boolean;
  error: boolean;
}

export const initAccountTransfer: AccountTransferState = {
  loading: false,
  loaded: false,
  error: false
};


export function accountTransferReducer(state: AccountTransferState = initAccountTransfer, action: fromTransfer.actions): AccountTransferState {

  switch (action.type) {

    case fromTransfer.TRANSFER_LOAD:
      return {
        ...state,
        loading: true
      };

    case fromTransfer.TRANSFER_SUCCESS:
      return {
        ...state,
        loading: false,
        loaded: true
      };

    case fromTransfer.TRANSFER_FAIL:
      return {
        loaded: false,
        loading: false,
        error: true
      };

    case fromTransfer.TRANSFER_RESET:
      return {
        loading: false,
        loaded: false,
        error: false
      };

    default:
      return state;
  }

}
