import { combineReducers } from '@ngrx/store';

import { accountTransferReducer as account_transfer } from '@store/reducers/models/account-transfer/account-tranfer.reducer';
import { productsReducer as products } from '@store/reducers/models/products/products.reducer';
import { movementReducer as movement } from '@store/reducers/models/movements/movement.reducer';
import { userReducer as user } from '@store/reducers/global/auth/auth.reducer';

export const models = combineReducers({
  products,
  user,
  movement,
  account_transfer
});
